import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import axios from "axios";
import { Form, Container, Button, Row, Col } from "react-bootstrap";
import ImageUploading from "react-images-uploading";
import { CategoryDirection } from "./CategoryBanner";
import AreaButton from "./AreaButton";
import Modal from "./Modal";

function ProductRegister({ history }) {
    const [modalOpen, setModalOpen] = useState(false);

    const [modalContents, setModalContents] = useState("");
    const closeModal = () => {
        setModalOpen(false);
    };
    const [area, setArea] = useState("  ");
    const { isLoggedIn, userData } = useSelector((state) => ({
        isLoggedIn: state.user.isLoggedIn,
        userData: state.user.payload,
    }));
    const { kind } = useSelector((state) => ({
        kind: state.category.payload,
    }));
    const [images, setImages] = useState([]);
    const maxNumber = 4;

    const onChangeImage = (imageList, addUpdateIndex) => {
        console.log(imageList, addUpdateIndex);
        setImages(imageList);
    };

    const onError = (errors, files) => {
        if (errors.maxNumber) {
            alert(` ${maxNumber}   `);
        }
    };

    const displayCategory = kind.map((k, index) => {
        return <option>{k.kind}</option>;
    });

    const [product, setProduct] = useState({
        name: "",
        category: kind[0].kind,
        price: null,
        quantity: 1,
        description: "",
    });

    const { name, category, price, quantity, description } = product;

    const onChangeHandler = (e) => {
        const { name, value } = e.target;
        setProduct({
            ...product,
            [name]: value,
        });
    };

    const onChangeArea = (e) => {
        const { name, value } = e.target;
        setArea(value);
    };

    const onClickHandler = (e) => {
        let category_id = kind[kind.findIndex((k) => k.kind === category)].pk;
        let image_list = images.map((img, index) => img.data_url);
        e.preventDefault();

        if (
            !(
                name &&
                category_id &&
                price &&
                quantity &&
                description &&
                image_list.length
            )
        ) {
            setModalOpen(true);
            setModalContents("   ");
            return;
        }
        if (
            area == undefined ||
            area == null ||
            area == "  "
        ) {
            setModalOpen(true);
            setModalContents("   ");
            return;
        }
        const body = {
            seller_id: userData.user_id,
            name: name,
            category_id: category_id,
            price: price,
            quantity: quantity,
            area: area,
            description: description,
            ...image_list, //   
        };

        axios
            .post("/apis/v1/product", body)
            .then((response) => {
                history.replace("/");
            })
            .catch((e) => {
                setModalOpen(true);
                setModalContents(
                    "  .  ."
                );
            });
    };

    return (
        <div>
            <Modal open={modalOpen} close={closeModal}>
                {modalContents}
            </Modal>

            <Container>
                <CategoryDirection tag1={""}></CategoryDirection>
                <br />
                <div style={{ fontSize: "1.3rem" }}>
                    <Row>
                        <Col
                            lg={{ span: 11, offset: 2 }}
                            sm={{ span: 12, offset: 0 }}
                            xs={{ span: 12 }}
                        >
                            <Form>
                                <Form.Group
                                    controlId="exampleForm.ControlSelect1"
                                    as={Row}
                                >
                                    <Form.Label column sm="2" lg="1" xs="4">
                                        
                                    </Form.Label>
                                    <Col sm="2" xs="8">
                                        <Form.Control
                                            as="select"
                                            name="category"
                                            onChange={onChangeHandler}
                                            value={category}
                                        >
                                            {displayCategory}
                                        </Form.Control>
                                    </Col>
                                </Form.Group>

                                <Form.Group
                                    controlId="exampleForm.ControlInput1"
                                    as={Row}
                                >
                                    <Form.Label column sm="2" lg="1" xs="4">
                                        
                                    </Form.Label>
                                    <Col sm="10" lg="8" xs="8">
                                        <Form.Control
                                            name="name"
                                            value={name}
                                            onChange={onChangeHandler}
                                            placeholder=" "
                                        />
                                    </Col>
                                </Form.Group>

                                <Form.Group
                                    controlId="exampleForm.ControlInput1"
                                    as={Row}
                                >
                                    <Form.Label column sm="2" lg="1" xs="4">
                                        
                                    </Form.Label>
                                    <Col sm="4" lg="2" xs="7">
                                        <Form.Control
                                            name="price"
                                            value={price}
                                            onChange={onChangeHandler}
                                            placeholder=" "
                                        />
                                    </Col>
                                    <Col xs="1">
                                        <p></p>
                                    </Col>
                                </Form.Group>

                                <Form.Group
                                    controlId="exampleForm.ControlInput1"
                                    as={Row}
                                >
                                    <Form.Label column sm="2" lg="1" xs="4">
                                        
                                    </Form.Label>
                                    <Col sm="2" lg="1" xs="8">
                                        <Form.Control
                                            name="quantity"
                                            value={quantity}
                                            onChange={onChangeHandler}
                                            placeholder=" "
                                        />
                                    </Col>
                                </Form.Group>

                                <Row>
                                    <Col
                                        sm="2"
                                        lg="1"
                                        xs="4"
                                        style={{ paddingTop: 20 }}
                                    >
                                         
                                    </Col>
                                    <Col
                                        sm="6"
                                        lg="6"
                                        xs="6"
                                        style={{ paddingTop: 8 }}
                                    >
                                        <AreaButton
                                            onChange={onChangeArea}
                                            area={area}
                                        />
                                    </Col>
                                </Row>
                                <br />
                                <Form.Group
                                    controlId="exampleForm.ControlTextarea1"
                                    as={Row}
                                >
                                    <Form.Label column sm="2" lg="1">
                                        
                                    </Form.Label>
                                    <Col sm="10" lg="8">
                                        <Form.Control
                                            as="textarea"
                                            rows={5}
                                            onChange={onChangeHandler}
                                            name="description"
                                            placeholder="  "
                                            value={description}
                                        />
                                    </Col>
                                </Form.Group>
                                <br />
                                <Form.Group
                                    controlId="exampleForm.ControlTextarea1"
                                    as={Row}
                                >
                                    <Form.Label>  </Form.Label>
                                    {/* <Button onClick={onClickGetImage}>  </Button> */}

                                    <ImageUploading
                                        multiple
                                        value={images}
                                        onChange={onChangeImage}
                                        maxNumber={maxNumber}
                                        dataURLKey="data_url"
                                        onError={onError} // 
                                    >
                                        {({
                                            imageList,
                                            onImageUpload,
                                            onImageUpdate,
                                            onImageRemove,
                                            isDragging,
                                            dragProps,
                                        }) => (
                                            // write your building UI

                                            <div className="upload__image-wrapper">
                                                <br />
                                                <Button
                                                    variant="outline-light"
                                                    style={{
                                                        background: "#e85255",
                                                        fontSize: "1.3rem",
                                                    }}
                                                    onClick={(e) => {
                                                        e.preventDefault();
                                                        onImageUpload();
                                                    }}
                                                    {...dragProps}
                                                >
                                                     
                                                </Button>
                                                <br />
                                                &nbsp;
                                                {imageList.map(
                                                    (image, index) => (
                                                        <div
                                                            key={index}
                                                            className="image-item"
                                                        >
                                                            <img
                                                                src={
                                                                    image[
                                                                        "data_url"
                                                                    ]
                                                                }
                                                                alt=""
                                                                width="700"
                                                            />
                                                            <div className="image-item__btn-wrapper">
                                                                <br />
                                                                <Button
                                                                    variant="outline-light"
                                                                    style={{
                                                                        background:
                                                                            "#e85255",
                                                                        fontSize:
                                                                            "1.3rem",
                                                                    }}
                                                                    onClick={(
                                                                        e
                                                                    ) => {
                                                                        e.preventDefault();
                                                                        onImageUpdate(
                                                                            index
                                                                        );
                                                                    }}
                                                                >
                                                                    
                                                                </Button>{" "}
                                                                <Button
                                                                    variant="outline-light"
                                                                    style={{
                                                                        background:
                                                                            "#e85255",
                                                                        fontSize:
                                                                            "1.3rem",
                                                                    }}
                                                                    onClick={(
                                                                        e
                                                                    ) => {
                                                                        e.preventDefault();
                                                                        onImageRemove(
                                                                            index
                                                                        );
                                                                    }}
                                                                >
                                                                    
                                                                </Button>
                                                            </div>
                                                            <br />
                                                        </div>
                                                    )
                                                )}
                                            </div>
                                        )}
                                    </ImageUploading>
                                    <Col>
                                        <p>
                                            {`  ${maxNumber}   `}
                                        </p>
                                        <br />
                                    </Col>
                                </Form.Group>
                                <Button
                                    type="submit"
                                    variant="outline-light"
                                    style={{
                                        background: "#e85255",
                                        fontSize: "1.3rem",
                                    }}
                                    onClick={onClickHandler}
                                >
                                     
                                </Button>
                            </Form>
                        </Col>
                    </Row>
                </div>
            </Container>
        </div>
    );
}

export default ProductRegister;
